import React, { FC, useEffect, useState } from "react";
import * as LucideIcon from "lucide-react";

import { colors, widthSizes } from "../../styleguide";

import {
	BackgroundContainer,
	Item,
	ListContainer,
	ListItem,
	MenuContainer,
	MenuProps,
	StyledCardVariation,
	StyledCardVariationProps,
	TextItem,
} from "./CardVariation.styles";
import GoalBackground from "./GoalBackground";
import TriggerBackground from "./TriggeredBackground";

export type IconName = keyof typeof LucideIcon;

interface CardVariationProps extends StyledCardVariationProps {
	dataQA: string,
	ariaLabel: string,
	menu?: MenuProps[],
	backgroundVariant?: "triggered" | "goals",
	className?: string
	children?: React.ReactNode
}

/** @deprecated this component has been deprecated */
const CardVariation: FC<CardVariationProps> = ({
	hasBackground = true,
	backgroundVariant = "triggered",
	widthSize = widthSizes.full,
	children,
	menu,
	dataQA,
	ariaLabel,
	mt,
	mb,
	ml,
	mr,
	m,
	className,
}) => {
	const [isMenuOpen, setIsMenuOpen] = useState(false);

	const handleOpenCloseMenu = () => setIsMenuOpen((prevState) => !prevState);

	useEffect(() => {
		document.documentElement.removeEventListener("click", handleOpenCloseMenu);
	}, [isMenuOpen]);

	return (
		<StyledCardVariation
			widthSize={widthSize}
			hasBackground={hasBackground}
			data-qa={dataQA}
			aria-label={ariaLabel}
			mt={mt}
			mb={mb}
			mr={mr}
			ml={ml}
			m={m}
			className={className}
		>
			{hasBackground && (
				<BackgroundContainer>
					{backgroundVariant === "goals" ? <GoalBackground /> : <TriggerBackground />}
				</BackgroundContainer>
			)}
			{children}
			{menu && menu.length > 0 && (
				<>
					<MenuContainer onClick={handleOpenCloseMenu}>
						<LucideIcon.EllipsisVertical />
					</MenuContainer>

					{isMenuOpen && (
						<ListContainer>
							{menu.map(({
								color = colors.black, icon, text, action,
							}) => {
								const Icon = LucideIcon[icon as IconName] as FC<{ color: string }>;
								return (
									<ListItem key={text} onClick={action} role="list">
										<Item>
											<Icon color={color} />
											<TextItem color={color}>{text}</TextItem>
										</Item>
									</ListItem>
								);
							})}
						</ListContainer>
					)}
				</>
			)}
		</StyledCardVariation>
	);
};

export default CardVariation;
